.App {
    text-align: center;
    max-width: 1370px;
    margin: auto;
    padding: 60px 0;
    position: relative;
    width: 100%;
    box-sizing: border-box;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}


/* Interactive elements should show the pointer cursor */

a,
button,
[role="button"],
input[type="submit"],
input[type="button"],
select {
    cursor: pointer;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}


/* Responsive styles */

@media screen and (max-width: 768px) {
    .App {
        padding: 40px 10px;
    }
}

@media screen and (max-width: 480px) {
    .App {
        padding: 20px 4px;
    }
}

@media screen and (max-width: 320px) {
    .App {
        padding: 10px 2px;
    }
}